html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-spartan);
  width: 100vw;
  height: 100vh;
}

h1, p, h2 {
  margin: 0;
}

.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: #3e52a3;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --mainBackground: hsl(222, 26%, 31%);
  --toggleBackground: hsl(223, 31%, 20%);
  --screenBackground: hsl(224, 36%, 15%);
  --keyBackgroundTheme1: hsl(225, 21%, 49%);
  --keyShadowTheme1: hsl(224, 28%, 35%);
  --keyBackgroundToggleTheme1: hsl(6, 63%, 50%);
  --keyShadowTheme1Red: hsl(6, 70%, 34%);
  --keyBackgroundTheme1Grey: hsl(30, 25%, 89%);
  --keyShadowTheme1Grey: hsl(28, 16%, 65%);
  --textColorDarkTheme1: hsl(221, 14%, 31%);
  --textWhite: hsl(0, 0, 100%);
  --mainBackgroundTheme2: hsl(0, 0%, 90%);
  --toggleBackgroundTheme2: hsl(0, 5%, 81%);
  --screenBackgroundTheme2: hsl(0, 0%, 93%);
  --keyBackgroundTheme2: hsl(185, 42%, 37%);
  --keyShadowTheme2: hsl(185, 58%, 25%);
  --keyBackgroundToggleTheme2: hsl(25, 98%, 40%);
  --keyShadowTheme2Dark: hsl(25, 99%, 27%);
  --keyBackgroundTheme2Grey: hsl(45, 7%, 89%);
  --keyShadowTheme2Grey: hsl(35, 11%, 61%);
  --textColorDarkTheme2: hsl(60, 10%, 19%);
  --mainBackgroundTheme3: hsl(268, 75%, 9%);
  --toggleBackgroundTheme3: hsl(268, 71%, 12%);
  --keyBackgroundTheme3: hsl(281, 89%, 26%);
  --keyShadowTheme3: hsl(285, 91%, 52%);
  --keyBackgroundToggleTheme3: hsl(176, 100%, 44%);
  --keyShadowTheme3Cyan: hsl(177, 92%, 70%);
  --keyBackgroundTheme3Violet: hsl(268, 47, 21%);
  --keyShadowTheme3Violet: hsl(290, 70%, 36%);
  --yellowTheme3: hsl(52, 100%, 62%);
  --blueTheme3: hsl(198, 20%, 13%);
}

:root {
  --font-spartan: "Spartan", sans-serif;
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

.calculator {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 0.625rem;
  max-width: 33.75rem;
  height: 44.25rem;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.main__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.output {
  display: flex;
  align-items: center;
  justify-content: end;
  width: 100%;
  margin-top: 0.9375rem;
}
.output__display {
  font-size: 3rem;
  font-weight: bold;
  padding: 2.1875rem;
}

.button__grid_container {
  display: grid;
  justify-content: center;
  margin-top: 1.5rem;
  padding: 2rem;
  width: 100%;
}

.button__container {
  display: grid;
  grid-template-rows: repeat(5, 1fr);
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
  grid-gap: 1.5625rem;
}

fieldset {
  position: relative;
  display: flex;
  justify-content: space-around;
  width: 4.4375rem;
  /*  padding: 0 1rem 1rem; */
  border: 0;
  text-align: center;
  padding: 0;
}
fieldset legend {
  display: inline-block;
  right: 4.6rem;
  top: 2.8rem;
  position: relative;
  font-size: 0.75rem;
  font-weight: bold;
  text-transform: uppercase;
}
fieldset label {
  display: inline-block;
  position: relative;
  width: 1rem;
  padding-bottom: 2rem;
  z-index: 2;
  cursor: pointer;
  font-size: 0.75rem;
  font-weight: bold;
}
fieldset label:first-of-type {
  z-index: 1;
}
fieldset div {
  width: 4.4375rem;
}
fieldset input {
  position: absolute;
  top: 0;
  height: 100%;
  width: 33%;
  appearance: none;
  background: none;
  opacity: 0.000001;
}
fieldset label:first-of-type::before {
  content: "";
  position: absolute;
  width: 4.4375rem;
  height: 1.625rem;
  bottom: 0;
  left: -0.5rem;
  background-color: #232d44;
  border-radius: 0.95rem;
}
fieldset label:last-of-type::after {
  content: "";
  position: absolute;
  height: 1rem;
  width: 1rem;
  background-color: #f96b5b;
  border-radius: 50%;
  bottom: 0.3125rem;
  right: 0.9375rem;
  transition: transform 0.3s ease;
}
fieldset input:first-of-type:checked ~ div > label:last-of-type::after {
  transform: translateX(-1.6rem);
}
fieldset input:nth-of-type(2):checked ~ div > label:last-of-type::after {
  transform: translateX(-0.4rem);
}
fieldset input:last-of-type:checked ~ div > label:last-of-type::after {
  transform: translateX(0.8rem);
}

.button {
  min-width: 6.3125rem;
  height: 4rem;
  font-size: 2rem;
  border-radius: 0.625rem;
}

.button__reset {
  grid-column-start: -5;
  grid-column-end: -3;
  grid-row-start: 5;
  grid-row-end: 6;
}

.button__equal {
  grid-column-start: -3;
  grid-column-end: -1;
  grid-row-start: 5;
  grid-row-end: -1;
}

.button--size {
  min-width: 14.1875rem;
}